	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Haircut","date":"08 Jan 2005","slug":"/2005-01-08-haircut/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/3111715/\" title=\"Christopher's a winner!\"><img src=\"http://photos2.flickr.com/3111715_686cbec1fb_m.jpg\" alt=\"\" border=\"0\" /></a></p>\n<p>Cool doo, dude! (He just won a game at <a href=\"http://www.wdrmaus.de/\">wdrmaus.de</a>)</p>"},"next":{"fields":{"title":"New Media-orites","date":"06 Jan 2005","slug":"/2005-01-06-new-media-orites/"}},"previous":{"fields":{"title":"Hagenbecks","date":"09 Jan 2005","slug":"/2005-01-09-hagenbecks/"}}},{"node":{"fields":{"title":"New Media-orites","date":"06 Jan 2005","slug":"/2005-01-06-new-media-orites/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/lumma/3018933/\"><img alt=\"nu2m.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/01/nu2m.jpg\" width=\"320\" height=\"240\" border=\"0\" /></a></p>\n<p>The <a title=\"Gathering of the Coders [Lummaland - das Weblog]\" href=\"http://lumma.de/eintrag.php?id=1107\">new IT Department</a> at New Media Management. And, as <a title=\"ameisendorf.de: Familienfoto\" href=\"http://ameisendorf.de/index.php?itemid=181\">Thorsten points out</a>, most of us have weblogs.</p>"},"next":{"fields":{"title":"Banner Ad","date":"03 Jan 2005","slug":"/2005-01-03-banner-ad/"}},"previous":{"fields":{"title":"Haircut","date":"08 Jan 2005","slug":"/2005-01-08-haircut/"}}},{"node":{"fields":{"title":"Banner Ad","date":"03 Jan 2005","slug":"/2005-01-03-banner-ad/","tags":"Y2005"},"html":"<p>My banner ad solicits donations to a <a href=\"http://www.aktion-deutschland-hilft.de/\">coalition of German aid organizations</a> for tsunami victims. Here are a few more places to donate, thanks to<br />\n<a href=\"http://www.jacobsen.no/anders/blog/\">Anders Jacobsen</a>. </p>\n<p>International aid organizations:<br />\n<a href=\"http://unicef.org/\">UNICEF</a> (United Nations Children's Fund)<br />\n<a href=\"http://www.wfp.org/\">United Nations' World Food Programme</a><br />\n<a href=\"http://www.msf.org/\">Medecins Sans Frontieres / Doctors without Borders</a> (<a href=\"http://www.msf.org/donations/index.cfm\">donate!</a>)<br />\n<a href=\"http://www.care.org/\">CARE International</a><br />\n<a href=\"http://www.ifrc.org/\">The International Federation of Red Cross and Red Crescent Societies</a></p>\n<p>UK/Europe:<br />\n<a href=\"http://www.dec.org.uk/\">Disasters Emergency Comittee (DEC)</a> - comprises a raft of aid agencies, including the below and others<br />\n<a href=\"http://www.redcross.org.uk/\">British Red Cross</a><br />\n<a href=\"http://www.savethechildren.org.uk/\">Save the Children UK</a></p>\n<p>North America:<br />\n<a href=\"http://www.redcross.org/\">American Red Cross</a><br />\n<a href=\"http://www.redcross.ca/\">Canadian Red Cross</a><br />\n<a href=\"http://www.savethechildren.org/\">Save The Children</a></p>\n<p><a href=\"http://www.jacobsen.no/anders/blog/\">Anders Jacobsen</a>: <a href=\"http://www.jacobsen.no/anders/blog/archives/2005/01/02/webloggers_give_to_tsunami_victims_and_ill_give_too.html\">Webloggers: Give to tsunami victims and I'll give too!</a></p>"},"next":{"fields":{"title":"Holiday Recovery","date":"02 Jan 2005","slug":"/2005-01-02-holiday-recovery/"}},"previous":{"fields":{"title":"New Media-orites","date":"06 Jan 2005","slug":"/2005-01-06-new-media-orites/"}}}],"pathPrefix":"","first":false,"last":false,"index":358,"pageCount":1101}}

